* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'open-sans', sans-serif;
  font-size: 20px;
}

div.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  color: #222;
  margin: 0 auto;
}

header{
    flex-basis: 30vh;
}

main{
    display: flex;
    flex-grow: 1;
    padding: 30px 0;
    
}

aside{
    min-width: 240px;
    
}

section.page{
    flex-grow: 1;
    
}

footer{
    background-color: black;
    color: white;
    padding: 10px 0;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 50px;
}

footer span{
    color: red;
}

footer p {
    font-size: 14px;
}
header {
    position: relative;
    overflow: hidden;
}

header img{
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    min-height: 100%;
    min-width: 100%;
}





nav {
  
  padding: 0 30px 30px 0;
}

.main ul {
  list-style-type: none;
}

nav.main a {
  display: block;
  text-decoration: none;
  color: black;
  text-transform: uppercase;
  font-weight: bold;
  padding: 10px;
  transition: .2s;
}

nav.main a:hover {
  -webkit-transform: translateX(20px);
          transform: translateX(20px)
}

nav.main a.active{
    border-left: 10px gray solid;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

nav.main ul li:nth-child(1) a.active{
    border-left-color: green;
}

nav.main ul li:nth-child(2) a.active{
    border-left-color: blue;
}

nav.main ul li:nth-child(3) a.active{
    border-left-color: red;
}

nav.main ul li:nth-child(4) a.active{
    border-left-color:orange;
}


.productslist h3{
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 26px;
}

.productslist ul{
    list-style-type: square;
    margin-left: 30px;
}

.productslist ul li{
    margin-bottom:30px ;

}

.productslist ul li a{
    display: block;
    text-decoration: none;
    color: grey;
}

.productslist a:hover{
    text-decoration: underline;
    color: black;
}

form h3 {
  margin-bottom: 5px;
}

form.contact textarea {
  padding-top: 2px;
  width: 80%;
  height: 30vh;
  font-size: 18px;
  line-height: 130%;
  margin-bottom: 5px;
}

.contact button {
  padding: 10px 20px;
  margin: 0 20% 0 auto;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 120%;
  display: block;
  cursor: pointer;
}

.contact button:hover {
  color: yellow;
  background-color: black;
}



.login input {
    padding: 5px 10px;
    font-size: 16px;
}

.login button{
    padding: 5px 10px;
    margin-left: 5px;
    text-transform: uppercase;
    cursor: pointer;
    font-weight: bold;
}
